.gridContainer {
  width: 100%;
  padding-top: 100%;
  position: relative;
}

.Grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; 
  background-image: -webkit-linear-gradient(45deg, black 25%, transparent 25%),
    -webkit-linear-gradient(135deg, black 25%, transparent 25%),
    -webkit-linear-gradient(45deg, transparent 75%, black 75%),
    -webkit-linear-gradient(135deg, transparent 75%, black 75%); 
  background-image: -o-linear-gradient(45deg, black 25%, transparent 25%),
    -o-linear-gradient(135deg, black 25%, transparent 25%),
    -o-linear-gradient(45deg, transparent 75%, black 75%),
    -o-linear-gradient(135deg, transparent 75%, black 75%); 
  background-image: linear-gradient(45deg, black 25%, transparent 25%),
    linear-gradient(-45deg, black 25%, transparent 25%),
    linear-gradient(45deg, transparent 75%, black 75%),
    linear-gradient(-45deg, transparent 75%, black 75%);
  background-size: 1.786% 1.786%;
  background-position: 0 0, 0 0.893%, 0.893% -0.893%, -0.893% 0px;
  background-color: white;
}

.Grid .col.black {
  background-color: black;
}
.Grid .col.unknown {
  border: 0.1px solid white;
}
.Grid .col.white {
  background-color: white;
}

.Grid .row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Grid .col {
  border: 1px solid black;
  -ms-flex-positive: 1;
      flex-grow: 1;
}



